import React, { useState, useRef, useCallback } from "react";
import "./App.css";
import About from "./pages/about";
import Projects from "./pages/projects";
import ThreeCanvas from "./pages/background";
import { Switch, Route, Link, HashRouter } from "react-router-dom";
import { isMobile } from "react-device-detect";

import Sun from "./assets/images/sun.png";
import Moon from "./assets/images/moon.png";

function App() {
  const [invert, setInvert] = useState(true);
  const classnames = ["App", invert && "App--inverted"].filter(Boolean);
  const mouse = useRef([0, 0]);
  const onMouseMove = useCallback(
    ({ clientX: x, clientY: y }) =>
      (mouse.current = [x - window.innerWidth / 2, y - window.innerHeight / 2]),
    []
  );

  function Content() {
    return (
      <>
        <Switch>
          <Route path="/projects">
            <header className={"App-header"}>
              <h1 className={"App-category-title"}>
                <Link to="/">Profile</Link>
              </h1>
              <h1 className={"App-category-title-clicked"}>Projects</h1>
              <div className={"App-invert-button"}>
                <img
                  src={invert ? Sun : Moon}
                  onClick={() => setInvert(!invert)}
                  className="App-invert-button-image"
                  alt="invert"
                ></img>
              </div>
            </header>
            <div className="App-body">
              <Projects />
            </div>
          </Route>
          <Route exact path="/">
            <header className={"App-header"}>
              <h1 className={"App-category-title-clicked"}>Profile</h1>
              <h1 className={"App-category-title"}>
                <Link to="/projects">Projects</Link>
              </h1>
              <div className={"App-invert-button"}>
                <img
                  src={invert ? Sun : Moon}
                  onClick={() => setInvert(!invert)}
                  className="App-invert-button-image"
                  alt="invert"
                ></img>
              </div>
            </header>
            <div className="App-body">
              <About />
            </div>
          </Route>
        </Switch>
        <div className="App-footer">
          <h1 className="App-category-title">© 2021</h1>
        </div>
      </>
    );
  }

  return (
    <HashRouter basename="/">
      <div
        className={classnames.join(" ")}
        id="App"
        onMouseMove={isMobile ? null : onMouseMove}
      >
        <ThreeCanvas
          classname="Three-canvas"
          mouseEvent={isMobile ? null : mouse}
        />
        <Content />
      </div>
    </HashRouter>
  );
}
export default App;
