import React, { useRef, useMemo } from "react";
import * as THREE from "three";
import { Canvas, useFrame, useThree, extend } from "react-three-fiber";
import { FilmPass } from "three/examples/jsm/postprocessing/FilmPass";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { GlitchPass } from "three/examples/jsm/postprocessing/GlitchPass";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";

extend({ EffectComposer, FilmPass, RenderPass, GlitchPass, UnrealBloomPass });

function Plane(props) {
  const mesh = useRef();
  const { viewport, size } = useThree();
  const width = size.width / viewport.width;
  const height = size.height / viewport.height;
  const texture = new THREE.TextureLoader(props.manager).load(
    "/assets/cube.jpg"
  );
  return (
    <mesh {...props} ref={mesh}>
      <planeGeometry args={[width / 4, height / 4, 12]} />
      <meshStandardMaterial map={texture} />
    </mesh>
  );
}

function Box(props) {
  const mesh = useRef();
  const texture = new THREE.TextureLoader(props.manager).load(
    "/assets/cube2.jpg"
  );
  const { size, viewport } = useThree();

  // Rotate mesh every frame, this is outside of React without overhead
  useFrame(() => {
    mesh.current.rotation.x = mesh.current.rotation.y += 0.001;
  });
  return (
    <mesh {...props} ref={mesh} rotation={[0, 0, 0]}>
      {size.width < 900 ? (
        <torusKnotBufferGeometry args={[viewport.width * 5, 0.35, 128, 20]} />
      ) : (
        <torusKnotBufferGeometry args={[viewport.width * 2, 0.7, 128, 20]} />
      )}
      <meshStandardMaterial map={texture} />
    </mesh>
  );
}

function MouseLight({ mouse }) {
  const { viewport, size } = useThree();
  const light = useRef();
  const aspect = size.width / viewport.width;

  useFrame(() => {
    let vec = new THREE.Vector3(
      -mouse.current[0] / aspect,
      mouse.current[1] / aspect,
      1
    );
    light.current.position.lerp(vec, 0.1);
  });

  return (
    <spotLight
      ref={light}
      penumbra={1}
      angle={Math.PI / 12}
      distance={1000}
      intensity={6}
      color="white"
      decay={0}
    />
  );
}

function Sphere({ mouse }) {
  const { viewport, size } = useThree();
  const aspect = size.width / viewport.width;
  const mesh = useRef();

  useFrame(() => {
    let vec = new THREE.Vector3(
      mouse.current[0] / aspect,
      -mouse.current[1] / aspect,
      0
    );
    mesh.current.position.lerp(vec, 0.1);
  });

  return (
    <mesh ref={mesh} visible position={[0, 0, 0]} rotation={[0, 0, 0]}>
      <sphereGeometry attach="geometry" args={[0.003, 16, 16]} />
      <meshBasicMaterial attach="material" color="white" />
    </mesh>
  );
}

function Effects() {
  const composer = useRef();
  const { scene, gl, camera } = useThree();
  const aspect = useMemo(() => new THREE.Vector2(512, 512), []);
  useFrame(() => composer.current.render(), 1);
  return (
    <effectComposer ref={composer} args={[gl]}>
      <renderPass attachArray="passes" scene={scene} camera={camera} />
      <unrealBloomPass attachArray="passes" args={[aspect, 2, 1, 0]} />
      <filmPass
        args={[0.2, 0.2, 100, true]}
        attachArray="passes"
        renderToScreen
      />
    </effectComposer>
  );
}

function ThreeCanvas({ mouseEvent }) {
  const manager = new THREE.LoadingManager();
  return (
    <Canvas camera={{ fov: 50, position: [0, 0, 1] }}>
      {mouseEvent != null ? <MouseLight mouse={mouseEvent} /> : null}
      {mouseEvent != null ? <Sphere mouse={mouseEvent} /> : null}
      <ambientLight intensity={0.5} />
      <Box position={[0, 0, -12]} manager={manager} />
      <Plane position={[0, 0, -20]} manager={manager} />
      <Effects />
    </Canvas>
  );
}
export default ThreeCanvas;
